body {
  margin: 0;
  padding: 0;
}

header {
  background-color: gray;
  padding:10px
}

nav ul {
  display: flex;
  margin: 10;
  padding:10;
}

.navLi {
  list-style: none;
  padding: 10px;
}

ul {
  display: block;
  list-style-type: disc;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 40px;
}

nav a {
  margin: 5px;
  text-decoration: none;
  color: white;
  background-color: gray;
  transition-duration: 1s;
}
nav a:hover {
  background-color: darkgrey;
  letter-spacing: 4px;
}

.profile{
  width: 320px;
  height: 320px;
  overflow: hidden;
  object-fit: cover;
}
.profile img{
  width: 320px;
  height: 320px;
  transition-duration: 1s;
}


.profile img:hover {
  width: 320px;
  height: 320px;
  transform: scale(1.2);
}

#name {
  font-size: 50px;
  font-weight:500;
}

main {
  padding: 32px;
}

#main2{
  display: grid;
  grid-template-columns: 1fr 1fr;
}
footer {
  margin: 0px;
  padding: 20px;
  color: black;
  background-color: lightgray;
}
@media (max-width: 768px) {
  main {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .box {
    display: block;
  }
}
